.projects-grid{
	display: flex;
}

.avatar-img {
	height: 250px;
	padding-top: 5em;
}

.banner-text {
	background-color: black;
	opacity: .8;
	width: 75%;
	margin: auto;
	border-radius: 10px;
}

.banner-text h1 {
	font-size: 66px;
	font-weight: bold;
	color: white;
}

.banner-text hr {
	border-top: 5px dotted white;
	width: 50%;
	margin: auto;
}

.banner-text p {
	color: white;
	font-size: 20px;
	padding: 1em;
}

.social-links {
	display: flex;
	justify-content: space-between;
	width: 50%;
	margin: auto;
}

.social-links i {
	color: white;
	font-size: 5em;
	padding: 8px;
}

.contact-body{
	margin: auto;
	position: fixed;
	width: 100%;
	height: 100%;
	justify-content:center;
	padding-top: 2em;	
}

.contact-grid{
	text-align: center;
	justify-content: center;
	width: 80%;
	height: 550px;
	background: white;
	color: black;
	margin-bottom: 20px;
}

.contact-grid h2 {
	font-family: 'Anton', sans-serif;
}

.contact-grid p {
	font-family: 'Oxygen', sans-serif;
	font-weight: bold;
}

.contact-grid hr {
	border-top: 5px dotted black;
	width: 50%;
	margin: auto;
}

.contact-list i {
	font-size: 66px;
	padding-right: 5rem;
}

/* Resume Page CSS */
.resume-right-col {
	background: #27221F;
	color: white;
	padding: 2em;
}

.resume-skills-progress .progressbar{
	background-color: #fcb045 !important;
}

.home-link{
	text-decoration: none;
	color: white;
}

.home-link:visited{
	color: inherit;
}

.card-title-php{
	color: #fff;
	height: 176px;
	background: url(./assets/SLICeLogo.png) center / cover;
}

.card-title-react{
	color: #fff;
	height: 176px;
	background: url(./assets/portfolio.png) center / cover;
}

.general-link{
	text-decoration: none;
}

.general-link:visited{
	color: inherit;
}

.list-item-contact {
	font-size: 30px;
	font-family: Anton;
}